.container {
  width: 100%;
  margin-top: 40px;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 18px;
  overflow-y: auto;
  padding: 0;
  margin: 0;
  list-style: none;
  padding-right: 8px;
}

.scroll::-webkit-scrollbar-track {
  margin-bottom: 64px;
}

.link {
  all: unset;
  cursor: pointer;
}
